"use client";

import React, { useState } from "react";
import { useParams, useRouter } from "next/navigation";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";
import { Badge } from "@/components/ui/badge";
import {
  ArrowLeft,
  ArrowRight,
  CheckCircle,
  Clock,
  BookOpen,
  PlayCircle,
  AlertCircle,
  Loader2,
  Home,
  Menu,
} from "lucide-react";
import { useCourseLearning } from "@/hooks/useCourseLearning";
import { ContentBlockRenderer } from "@/components/content/ContentBlockRenderer";
import { CourseSidebar } from "@/components/course/CourseSidebar";
import {
  QuizQuestion,
  QuizProgress,
  QuizResults,
} from "@/components/quiz/QuizQuestion";

export default function CourseLearningPage() {
  const params = useParams();
  const router = useRouter();
  const courseSlug = params.slug as string;

  const {
    learningData,
    loading,
    error,
    currentSectionIndex,
    navigateToSection,
    navigateToNextSection,
    navigateToPreviousSection,
    markSectionComplete,
    trackInteraction,
    submitQuizAnswer,
    getSectionQuizResults,
    isSectionAccessible,
    quizAttempts,
  } = useCourseLearning(courseSlug);

  const [showQuiz, setShowQuiz] = useState(false);
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [quizAnswers, setQuizAnswers] = useState<Record<string, string>>({});
  const [showQuizResults, setShowQuizResults] = useState(false);

  // Show loading state
  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 dark:bg-gray-900 flex items-center justify-center">
        <div className="text-center">
          <Loader2 className="h-8 w-8 animate-spin mx-auto mb-4" />
          <h2 className="text-xl font-semibold mb-2">Loading Course</h2>
          <p className="text-muted-foreground">
            Please wait while we load your learning content...
          </p>
        </div>
      </div>
    );
  }

  // Show error state
  if (error || !learningData) {
    return (
      <div className="min-h-screen bg-gray-50 dark:bg-gray-900 flex items-center justify-center">
        <div className="text-center max-w-md">
          <AlertCircle className="h-12 w-12 text-red-500 mx-auto mb-4" />
          <h2 className="text-xl font-semibold mb-2">Error Loading Course</h2>
          <p className="text-muted-foreground mb-6">
            {error || "Course not found"}
          </p>
          <div className="space-x-4">
            <Button onClick={() => router.push("/courses")}>
              <Home className="h-4 w-4 mr-2" />
              Back to Courses
            </Button>
            <Button variant="outline" onClick={() => window.location.reload()}>
              Try Again
            </Button>
          </div>
        </div>
      </div>
    );
  }

  const { course, sections, currentSection, progress } = learningData;
  const currentSectionData = sections[currentSectionIndex];
  const hasQuiz =
    currentSectionData?.questions && currentSectionData.questions.length > 0;
  const isSectionComplete = progress.completedSections.includes(
    currentSectionData?.id || ""
  );

  // Quiz handling functions
  const handleQuizStart = () => {
    setShowQuiz(true);
    setCurrentQuestionIndex(0);
    setQuizAnswers({});
    setShowQuizResults(false);
  };

  const handleQuizAnswer = (questionId: string, answer: string) => {
    setQuizAnswers(prev => ({ ...prev, [questionId]: answer }));
  };

  const handleQuizNext = async () => {
    if (currentSectionData?.questions) {
      const currentQuestion =
        currentSectionData.questions[currentQuestionIndex];

      // Submit current answer if not already submitted
      if (!quizAnswers[currentQuestion.id]) {
        const answer = quizAnswers[currentQuestion.id];
        if (answer) {
          await submitQuizAnswer(currentQuestion.id, answer);
        }
      }

      if (currentQuestionIndex < currentSectionData.questions.length - 1) {
        setCurrentQuestionIndex(currentQuestionIndex + 1);
      } else {
        // Quiz completed
        setShowQuizResults(true);
      }
    }
  };

  const handleQuizRetake = () => {
    setCurrentQuestionIndex(0);
    setQuizAnswers({});
    setShowQuizResults(false);
  };

  const handleQuizContinue = () => {
    setShowQuiz(false);
    setShowQuizResults(false);
    // Mark section as complete if quiz passed
    if (currentSectionData?.id) {
      markSectionComplete(currentSectionData.id);
    }
  };

  const handleSectionComplete = async () => {
    if (currentSectionData?.id) {
      await markSectionComplete(currentSectionData.id);

      // Move to next section if available
      if (currentSectionIndex < sections.length - 1) {
        navigateToNextSection();
      } else {
        // Course completed
        router.push(`/courses/${courseSlug}/complete`);
      }
    }
  };

  const formatTimeSpent = (seconds: number) => {
    const hours = Math.floor(seconds / 3600);
    const minutes = Math.floor((seconds % 3600) / 60);

    if (hours > 0) {
      return `${hours}h ${minutes}m`;
    }
    return `${minutes}m`;
  };

  // Quiz results calculation
  const quizResults = currentSectionData
    ? getSectionQuizResults(currentSectionData.id)
    : { total: 0, correct: 0, percentage: 0 };

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
      {/* Header */}
      <div className="bg-white dark:bg-gray-800 border-b border-border">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center gap-4">
              <Button
                variant="ghost"
                size="sm"
                onClick={() => router.push(`/courses/${courseSlug}`)}
                className="flex items-center gap-2"
              >
                <ArrowLeft className="h-4 w-4" />
                Back to Course
              </Button>

              <div className="hidden sm:block">
                <h1 className="text-lg font-semibold truncate max-w-md">
                  {course.title}
                </h1>
                <p className="text-sm text-muted-foreground">
                  {currentSectionData?.title}
                </p>
              </div>
            </div>

            <div className="flex items-center gap-4">
              <div className="hidden sm:flex items-center gap-2 text-sm text-muted-foreground">
                <Clock className="h-4 w-4" />
                <span>{formatTimeSpent(progress.timeSpent)}</span>
              </div>

              <div className="flex items-center gap-2">
                <span className="text-sm font-medium">
                  {Math.round(progress.overallProgress)}%
                </span>
                <Progress
                  value={progress.overallProgress}
                  className="w-20 h-2"
                />
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="flex gap-8">
          {/* Sidebar */}
          <div className="hidden lg:block">
            <CourseSidebar
              sections={sections}
              currentSectionId={currentSectionData?.id}
              completedSections={progress.completedSections}
              onSectionSelect={navigateToSection}
              isSectionAccessible={isSectionAccessible}
              overallProgress={progress.overallProgress}
              timeSpent={progress.timeSpent}
              courseTitle={course.title}
            />
          </div>

          {/* Main Content */}
          <div className="flex-1 min-w-0">
            {!showQuiz ? (
              <div className="space-y-6">
                {/* Section Header */}
                <Card>
                  <CardHeader>
                    <div className="flex items-center justify-between">
                      <div>
                        <CardTitle className="flex items-center gap-3">
                          <BookOpen className="h-6 w-6" />
                          {currentSectionData?.title}
                        </CardTitle>
                        <p className="text-muted-foreground mt-2">
                          Section {currentSectionIndex + 1} of {sections.length}
                        </p>
                      </div>

                      {isSectionComplete ? (
                        <Badge
                          variant="secondary"
                          className="bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200"
                        >
                          <CheckCircle className="h-4 w-4 mr-1" />
                          Completed
                        </Badge>
                      ) : (
                        <Badge variant="outline" className="text-sm">
                          Click "Mark Complete" to unlock next section
                        </Badge>
                      )}
                    </div>
                  </CardHeader>
                </Card>

                {/* Content Blocks */}
                <div className="space-y-6">
                  {currentSectionData?.blocks.map(block => (
                    <ContentBlockRenderer
                      key={block.id}
                      block={block}
                      sectionId={currentSectionData.id}
                      onInteraction={trackInteraction}
                    />
                  ))}
                </div>

                {/* Section Navigation */}
                <Card>
                  <CardContent className="p-6">
                    <div className="flex items-center justify-between">
                      <Button
                        variant="outline"
                        onClick={navigateToPreviousSection}
                        disabled={currentSectionIndex === 0}
                        className="flex items-center gap-2"
                      >
                        <ArrowLeft className="h-4 w-4" />
                        Previous Section
                      </Button>

                      <div className="flex items-center gap-3">
                        {hasQuiz && !isSectionComplete && (
                          <Button
                            variant="outline"
                            onClick={handleQuizStart}
                            className="flex items-center gap-2"
                          >
                            <PlayCircle className="h-4 w-4" />
                            Take Quiz
                          </Button>
                        )}

                        <Button
                          onClick={handleSectionComplete}
                          disabled={isSectionComplete}
                          className="flex items-center gap-2"
                        >
                          {isSectionComplete ? (
                            <>
                              <CheckCircle className="h-4 w-4" />
                              Section Complete
                            </>
                          ) : currentSectionIndex === sections.length - 1 ? (
                            <>
                              <CheckCircle className="h-4 w-4" />
                              Complete Course
                            </>
                          ) : (
                            <>
                              <CheckCircle className="h-4 w-4" />
                              Mark Complete
                            </>
                          )}
                        </Button>
                      </div>

                      <Button
                        onClick={navigateToNextSection}
                        disabled={
                          currentSectionIndex === sections.length - 1 ||
                          !isSectionAccessible(currentSectionIndex + 1)
                        }
                        className="flex items-center gap-2"
                      >
                        Next Section
                        <ArrowRight className="h-4 w-4" />
                      </Button>
                    </div>
                  </CardContent>
                </Card>
              </div>
            ) : showQuizResults ? (
              /* Quiz Results */
              <QuizResults
                totalQuestions={quizResults.total}
                correctAnswers={quizResults.correct}
                onRetake={handleQuizRetake}
                onContinue={handleQuizContinue}
                passingScore={80}
              />
            ) : (
              /* Quiz Interface */
              <div className="space-y-6">
                <QuizProgress
                  currentQuestion={currentQuestionIndex + 1}
                  totalQuestions={currentSectionData?.questions?.length || 0}
                  correctAnswers={quizResults.correct}
                />

                {currentSectionData?.questions &&
                  currentSectionData.questions[currentQuestionIndex] && (
                    <QuizQuestion
                      question={
                        currentSectionData.questions[currentQuestionIndex]
                      }
                      onAnswer={answer =>
                        handleQuizAnswer(
                          currentSectionData.questions[currentQuestionIndex].id,
                          answer
                        )
                      }
                      onNext={handleQuizNext}
                      hasNext={
                        currentQuestionIndex <
                        currentSectionData.questions.length - 1
                      }
                      userAnswer={
                        quizAnswers[
                          currentSectionData.questions[currentQuestionIndex].id
                        ]
                      }
                    />
                  )}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
